<template>
  <section class="container">
    <div class="d-flex justify-content-between py-2">
      <div class="next-episode" v-if="next">
        <b-button
          native-type="submit"
          variant="info"
          class="btn custom-button"
          @click.prevent="nextEpisodeListener"
          :loading="loadingNext"
        >
          <i class="fas fa-caret-right pl-2"></i>
          پخش بعدی
        </b-button>
      </div>

      <div class="unit-complete">
        <b-button
          native-type="submit"
          variant="default"
          pill
          class="btn btn-round custom-button"
          @click="markAsDoneListener"
          :loading="loadingMarkAsDone"
          v-if="canMarkAsDone"
          :class="isDoneEpisode ? 'btn-success ' : 'btn-danger '"
        >
          {{ isDoneEpisode ? 'این بخش را ندیدم' : 'این بخش را دیدم' }}
          <i
            :class="
              'fas fa-' +
              (isDoneEpisode ? 'eye-slash text-white ' : 'eye text-white ') +
              'pr-2'
            "
          ></i>
        </b-button>
      </div>

      <div class="previous-episode" v-if="prev">
        <b-button
          native-type="submit"
          variant="info"
          class="btn custom-button"
          @click.prevent="prevEpisodeListener"
          :loading="loadingPrev"
        >
          پخش قبلی
          <i class="fas fa-caret-left pr-2"></i>
        </b-button>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  props: {
    next: Boolean,
    prev: Boolean,
    loadingNext: Boolean,
    loadingPrev: Boolean,
    isDoneEpisode: Boolean,
    canMarkAsDone: Boolean,
    loadingMarkAsDone: Boolean,
  },
  methods: {
    nextEpisodeListener(e) {
      this.$emit('nextLesson');
    },
    prevEpisodeListener(e) {
      this.$emit('prevLesson');
    },
    markAsDoneListener(e) {
      this.$emit('markAsDone');
    },
  },
};
</script>

